<template>
    <div class="place-bid-input">
        <button class="primary-btn" @click="placeBid">bid</button>
        <input min="0" type="Number" v-model="price" />
        <span class="currency">{{currency}}</span>
    </div>
</template>

<script>
import io from "socket.io-client";
import { bus } from "../../main";
export default {
    name: "PlaceBidInput",
    props: {
        currency: {
            type: String,
            default: "USD"
        },
        auctionId: {
            type: String
        }
    },
    data () {
        return {
            price: 0,
            socket: io("https://localhost:3000")
        };
    },
    methods: {
        async placeBid () {
            try {
                const response = await this.$http.patch(`/api/auction/${this.auctionId}`, { price: this.price });
                this.socket.emit("liveBid", { roomId: this.auctionId, bid: response.data.bid });
            } catch (error) {
                bus.$emit("changeMessage", error.response.data.msg.price, "error");
            }
        }
    }
};
</script>

<style lang="scss" >
.place-bid-input {
    background: rgb(177, 177, 177);
    margin: 1rem 0;
    border-radius: 10px;
    overflow: hidden;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    .primary-btn {
        border-radius: 0;
        font-size: 1.1rem;
        outline: none;
    }
    input {
        font-family: "Secular One", sans-serif;
        border: none;
        background: transparent;
        font-size: 1.2rem;
        width: 7rem;
        padding: 0 0.3rem;
        outline: none;
    }
    .currency {
        margin-right: 0.5rem;
    }
}
</style>
