<template>
    <div class="auction-card-live-container">
        <div class="main-auction-info">
            <v-image :imageUrl="image" />
            <h3 class="auction-name">{{name}}</h3>
        </div>
        <div class="biding-table">
            <span class="time">{{now}}</span>
            <bid-card
                v-for="bid in bids"
                :key="bid.id"
                :timeStamp="new Date(bid.timeStamp)"
                :price="parseFloat(bid.price)"
                :username="bid.author.username"
            />
        </div>
        <div class="input-bid-container">
            <place-bid-input v-if="isAuctionLive" :auctionId="id" />
            <router-link class="go-to-link" :to="'/auction/' + this.id">go to</router-link>
        </div>
    </div>
</template>

<script>
import BidCard from "@/components/BidCard.vue";
import { PlaceBidInput } from "@/components/Inputs";
import liveTime from "@/_helper/dateCountDown";
import VImage from "@/components/VImage";
import io from "socket.io-client";

export default {
    name: "AuctionCardLive",
    components: {
        BidCard,
        PlaceBidInput,
        VImage
    },
    data () {
        return {
            socket: io("https://localhost:3000"),
            now: "",
            liveCountDownInterval: null,
            myBid: "",
            isAuctionLive: false
        };
    },
    props: {
        id: {
            type: String
        },
        name: {
            type: String
        },
        image: {
            type: String
        },
        price: {
            type: Number
        },
        endDate: {
            type: Date
        },
        bids: {
            type: Array
        }
    },
    created () {
        this.bids = this.bids.reverse();
        this.joinBiddingRoom();
        this.isAuctionLive = !this.isEnded;
        this.liveAuctionCountDown();
    },
    beforeDestroy () {
        clearInterval(this.liveCountDownInterval);
        this.leaveBiddingRoom();
    },
    methods: {
        joinBiddingRoom () {
            this.socket.emit("joinBidding", { room: this.id });

            this.socket.on("sendBid", data => {
                this.bids.unshift(data);
            });
        },
        leaveBiddingRoom () {
            this.socket.emit("leaveBidding", {
                room: this.id
            });
            this.socket.removeAllListeners();
        },
        liveAuctionCountDown () {
            const nowTime = new Date();
            let distance, tempLiveCountdown;
            if (nowTime < this.endDate) {
                this.now = liveTime(this.endDate);
                distance = this.endDate - nowTime;
                if (distance > 0) {
                    const myInternval = distance <= 3600000 ? 1000 : 60000;
                    this.liveCounDownInterval = setInterval(() => {
                        tempLiveCountdown = liveTime(this.endDate);
                        this.isAuctionLive = tempLiveCountdown !== "expired";
                        this.now = tempLiveCountdown;
                    }, myInternval);
                }
            }
        }
    },
    computed: {
        isEnded () {
            const timeNow = new Date();
            const endDateParsed = new Date(this.endDate);
            return timeNow > endDateParsed;
        }
    }
};
</script>

<style lang="scss">
.auction-card-live-container {
    display: grid;
    grid-template-columns: 2fr 3fr;
    background: white;
    box-shadow: 0 1px 1px 1px rgba(0, 0, 0, 0.361);
    border-radius: 10px;
    overflow: hidden;
    margin: 1rem 0;
    height: 15rem;
    width: 35rem;
    flex-wrap: wrap;
    transition: all 0.3s ease-in-out;

    @include breaking-point-sm {
        grid-template-columns: 1fr;
        height: 23rem;
        width: 19rem;
    }

    .main-auction-info {
        background: rgb(248, 248, 248);
        display: flex;
        flex-direction: column;
        overflow: hidden;
        width: 15rem;
        height: 100%;
        @include breaking-point-sm {
            width: 100%;
        }
        img {
            width: 16rem;
            height: 6rem;
            object-fit: cover;
            @include breaking-point-sm {
                width: 100%;
            }
        }
        .auction-name {
            margin: 0;
            font-size: 1rem;
            margin: 0.1rem 0.3rem;
            flex-grow: 1;
            color: $main-dark-blue;
        }
    }

    .biding-table {
        display: block;
        text-align: center;
        overflow-y: scroll;
        overflow-x: hidden;
        padding: 0 0.5rem;
        flex-grow: 1;
        .time {
            margin-top: 0.5rem;
            color: $main-dark-blue;
        }
    }

    .input-bid-container {
        background: rgb(211, 211, 211);
        width: 100%;
        height: 100%;
        max-height: 3rem;
        grid-column: 1/3;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        align-self: flex-end;

        @include breaking-point-sm {
            padding: 0 0.2rem;
            grid-column: auto;
        }

        .go-to-link {
            color: $main-dark-blue;
            text-decoration: none;
            margin: 0 0.5rem;
            &:hover {
                text-decoration: underline;
            }
        }
        .place-bid-input {
            margin: 0;
            margin-left: 0.5rem;
            input {
                width: 8rem;
                @include breaking-point-sm {
                    width: 5rem;
                }
            }
        }
    }
}
</style>
