<template>
    <div class="pagination-container">
        <div class="btn-container" :class="{show: prev}">
            <button @click="prevPage" class="pgination-btn">Prev</button>
        </div>
        <span v-if="prev || next" class="selected">{{this.currentPage}}</span>
        <div class="btn-container" :class="{show: next}">
            <button @click="nextPage" class="pgination-btn">Next</button>
        </div>
    </div>
</template>

<script>
export default {
    name: "Pagination",
    model: {
        prop: "currentPage",
        event: "click"
    },
    props: {
        prev: null,
        next: null,
        currentPage: null
    },
    methods: {
        nextPage () {
            this.$emit("click", this.currentPage + 1);
        },
        prevPage () {
            this.$emit("click", this.currentPage - 1);
        }
    }
};
</script>

<style lang="scss">
.pagination-container {
    display: flex;
    flex-direction: row;
    width: 100%;
    align-items: center;
    justify-content: center;

    .selected {
        padding: 0.5rem;
        border-radius: 10rem;
        box-shadow: 0 1px 1px 1px rgba(0, 0, 0, 0.421);
        width: 0.8rem;
        height: 0.8rem;
        display: flex;
        justify-content: center;
        align-items: center;
        font-family: "Secular One", sans-serif;
        margin: 0.5rem;
        outline: none;
        background: $main-dark-blue;
        color: white;
    }
    .pgination-btn {
        cursor: pointer;
        font-family: "Secular One", sans-serif;
        color: $main-dark-blue;
        border: none;
        border: none;
        outline: none;
        font-size: 1.1rem;
        background: transparent;
        display: none;
    }
    .btn-container {
        width: 4rem;
        &.show {
            .pgination-btn {
                display: flex;
            }
        }
    }
}
</style>
