import axios from "axios";
import router from "../../router";

const state = {
    user: null
};

const getters = {
    getUser: (state) => state.user
};

const actions = {
    LOGOUT: ({ commit }) => {
        axios.get("https://localhost:3000/api/auth/logout", { withCredentials: true })
            .then(() => {
                router.push("/").catch(() => { });
                commit("UPDATE_USER", null);
            });
    }
};

const mutations = {
    UPDATE_USER (state, username) {
        state.user = username;
    }
};

export default {
    state,
    getters,
    actions,
    mutations
};
